@import './Variables.scss';
.cart-wrapper {
    display: inline-flex;
    justify-content: center;
    width: 100%;
    margin: 15px auto;
    overflow-y: scroll;
}
.cart-container {
    background-color: $yellow;
    padding: 10px;
    color: $heavy_green;
    border-radius: $radius;
    text-align: center;
    display: inline-block;
}
.clear-cart{
    display: inline-block;
    border: 1px solid $lightgreen;
    padding: 0px;
    border-radius: 5px;
    background-color: $lightgreen;
    cursor: pointer;
}
.title-cart {
    font-size: 18px;
    font-weight: bold;
    color: $heavy_green;
    padding: 10px 0;
    text-align: left;
}

.background-cart{
    position: fixed;
    width: 100%;
    height: 100%;
    right: 0;
    top:0;
    background-color: $greyPopUp;
    z-index: 1
}

.cart-wrapper {
    position: fixed;
    height: 100%;
    top: 0;
    right: 0;
    z-index: 1;
    margin-top: 0;
    width: 350px;
    margin-right: 0 !important;
    background-color: $white;
    border-radius: $radius 0 0 $radius;
    background-color: $electricgreen;
}
.cart-container {
    background-color: transparent;
    width: 100%
}

.close-cart {
    display: inline-block;
    cursor: pointer;
}
.mini-cart-items-container {
    margin: 0;
}
.opened-cart{
    overflow: hidden;
}
.mini-cart-item {
    border-radius: $radius;
    background-color: $white;
    padding: 10px 5px;
    margin: 5px auto;
    display: inline-flex;
    width: calc(100% - 10px);
}
.mini-cart-item-posotita{
    width: 10%;
}
.mini-cart-item-title{
    width: 70%;
    text-align: left;
}
.mini-cart-item-price{
    width: 20%;
}


.header-mini-cart{
    display: inline-flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}
.bi.bi-x-square{
    font-size: 22px;
}