@import './Variables.scss';
.thankyou-wrapper {
    background-color: $white;
    z-index: 0;
    position: relative;
}
.thankyou-container{
    padding-bottom: 5px;
}
.thankyou-title {
    font-weight: 700;
    text-align: center;
    background-color: $yellow;
    padding: 10px;
}

.thankyou-content {
    width: 100%;
    max-width: 700px;
    margin: 20px auto;
    border-radius: $radius;
    overflow: hidden;
}

.thankyou-content-title {
    text-align: center;
}

.thankyou-left {
    font-weight: 600;
    width: 50%;
}
.thank-you-sides{
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-start;
    padding: 2px 0 2px 10px;}

.thankyou-content-title {
    margin: 0;
    text-align: center;
    font-weight: 600;
    padding-left: 10px;
    padding: 10px;
    border-bottom: 2px dotted $lightgreen;
}

.thankyou-info-container {
    background-color:  $white;
    margin: 10px 0 20px;
}
.thankyou-right{
    width: 50%;
    overflow-wrap: break-word;
}
@media (max-width: $mobile){
    .thankyou-left, .thankyou-right{
        width: 100%;
        }
        .thankyou-right{
        color :$heavy_green;
        }
        .thankyou-left{
        color:$heavy_green;

        }
    .thank-you-sides{
        flex-wrap: wrap;
        padding-bottom: 10px
    }
}