@import "./Variables.scss";
.social-media-container {
  display: inline-block;
  padding: 10px;
  margin: 0 auto;
  text-align: center;
  border-radius: 10px;
}
.social-media {
  margin: 0 auto;
  text-align: center;
  display: inline-flex;
  justify-content: center;
  width: 100%;
  i {
    color: $green;
    margin: 0 10px;
    cursor: pointer;
  }
}

.contact_text {
  text-align: center;
  margin: 0px auto;
  color: $green;
  font-weight: 600;
  font-style: italic;
}
.social-media-container.padding-bottom {
  padding-bottom: 50px;
}

.footer-line.footer-title {
  color: $green;
}

.social-media-container {
  display: flex;
  justify-content: flex-start;
  width: 100%;
  text-align: left;
  margin-left: 0;
  padding-left: 0;
}

.social-media {
  text-align: left;
  justify-content: flex-start;
}
