@import './Variables.scss';
.order-item {
    background-color: $white;
    margin: 10px auto;
    padding: 5px;
}

.orders-wrapper {
    padding: 10px;
    max-width: 1200px;
    margin: 0 auto;
}
.procucts-for-order {
    background-color: $lightgreen;
    padding: 10px;
}

.order-title {
    text-align: left;
    font-weight: bold;
    font-size: 20px;
    color: $heavy_green;
}

.customer-info-container{
    display: inline-flex;
    flex-wrap: wrap;
    width: calc(100% - 20px);
    padding: 10px;
}
.customer-info-container div{
    width: 50%
} 
@media (min-width: $mobile) { 
    .customer-info-container div{
        width: 25%
    }
}
.order-item {
    position: relative;
}
.delete-this-order{
    cursor: pointer;
    color: $white;
    display: inline-block;
    background-color: $red;
    padding: 5px;
    border-radius: 4px;
    margin: 5px 0;
    font-size: 14px;
}
.orders-item-buttons {
    text-align: center;
}