@import './Variables.scss';
.button-complete-order {
    position: fixed;
    background-color: $purple;
    width: calc(100% - 40px);
    height: auto;
    padding: 15px;
    left: 0;
    bottom: 0;
    cursor: pointer;
    text-align: center;
    color: $white;
    font-size: 14px;
    border-radius: 10px;
    font-weight: bold;
    margin: 5px;
}

@media (min-width: 800px){
    .button-complete-order{
        display: none;
    }
}
.social-media-container{
    margin-bottom: 10px;        
    }
@media (min-width: 800px){
    .social-media-container{
        margin-bottom: 10px            
    }
}