@import "./Variables.scss";
.product {
  background-color: $white;
  margin: 5px auto;
  display: flex;
  border-radius: 5px;
  padding: 5px;
  flex-wrap: wrap;
  align-items: center;
  box-shadow: 0 0 10px -3px $greenforshadow;
}

.firstcol {
}

.title {
  font-weight: 600;
  color: $purple;
}
.thirdcol {
  display: flex;
  justify-content: flex-end;
}
.fullPrice {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.description {
  width: 100%;
  font-weight: 500;
  font-size: 14px;
  color: $greybrown;
}
.quantity {
  color: $greybrown;
  margin: 0 4px;
}
.price {
  font-weight: bold;
  color: $heavy_green;
}

.initPrice {
  color: $greybrown;
  text-decoration: line-through;
  padding: 0 10px;
  font-weight: 600;
  font-size: 12px;
}
.thirdcol i.bi {
  color: $purple;
  font-size: 20px;
  cursor: pointer;
}
.product {
  width: calc(100% - 20px);
  margin-left: 5px;
}

.firstcol {
  width: 55%;
}
.secondcol {
  width: 20%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.thirdcol {
  width: 25%;
}
.fullPrice {
  display: block;
  line-height: 14px;
}
@media (min-width: $mobile) {
  .fullPrice {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  .product {
    width: calc(50% - 20px);
    margin-left: 5px;
  }
  .firstcol {
    width: 65%;
  }
  .secondcol {
    width: 20%;
  }
  .thirdcol {
    width: 15%;
  }
}

.product-inner {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.product {
  display: inline-flex;
  align-items: center;
  flex-wrap: nowrap;
}
.product .image-prod {
  width: 20%;
  margin-right: 10px;
}
.product .image-prod img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}
