@import './Variables.scss';

.category_name{
    font-weight: bold;
    cursor: pointer;
    color: $white;
    position: relative;
    padding: 10px;
    background-color: $green;
    border-radius: $radius;
}

.category_outer.hidden_category .category_name {
    color: $white;
}
.category_outer {
    margin: 5px 0 10px;
    padding: 0;
    border-radius: 5px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
     -khtml-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.hidden_category .category_name{
    border-radius: $radius;
}

.category_name:after{
    content:"";
    position: absolute;
    top: 5px;
    right: 15px;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-top: 7px solid $white; 
    transform: rotate(180deg); 
    margin-top: 10px;
    transition: .35s;
}
.hidden_category .category_name:after{
    transform: rotate(360deg);
    margin-top: 15px;
    border-top: 7px solid $white;
}
.categorys_products{
    background-color: $lightgreen;
    width: calc( 100% - 20px);
    margin: 0 auto;
    justify-content: center;
    margin-left: 10px;
    border-radius: 0 0 $radius $radius;
    display: inline-flex;
    flex-wrap: wrap;
    padding-bottom: 15px;
    padding-top: 15px;
}
.hidden_category .categorys_products{    
    display: none;
}