@import "./Variables.scss";
.delivery-disclaimer-container {
  border-radius: $radius;
  font-size: 14px;
  text-align: center;
}
.delivery-disclaimer-free-delivery-cost {
  padding: 10px;
  border-radius: $radius;
  font-size: 14px;
  text-align: center;
  font-weight: 700;
  background-color: $lightgreen;
  color: $heavy_green;
}
.delivery-disclaimer-second-message {
  background-color: $yellow;
  padding: 10px;
  border-radius: $radius;
  font-size: 14px;
  text-align: center;
  font-weight: 700;
  color: $heavy_green;
}
.delivery-disclaimer-win-delivery-msg {
  background-color: $lightgreen;
  padding: 10px;
  border-radius: $radius;
  font-size: 14px;
  text-align: center;
  font-weight: 700;
  color: $purple;
}
.delivery-disclaimer-wrapper {
  margin: 10px auto;
}
.thankyou-page .delivery-disclaimer-wrapper {
  display: none;
}
